import React from "react";
import "./styles.css";
import logo from "../../images/logo.png";
import flowers from '../../images/flores.svg'
import PDF from '../../images/juani-sabbatini-cv.pdf'

export default function Home() {
  document.title = "Juani Sabbatini"
  return (
      <div className="Home">
        <div className="name">
          <h1>HOLA, SOY JUANI SABBATINI</h1>
          <p>Desarrollador Fullstack en Javascript con una gran pasión por la programación.</p>
          <a href={PDF} download={PDF}>
         <button type="button">Mira mi CV</button> 
         </a> 
        </div>
        <img src={logo} className="developer" alt="Developer" />
      <img className="flowers" src={flowers} alt="Flowers"/>
      </div>
  );
}
