import React from "react";
import "./styles.css";
import marioLuigi from "../../images/mario-luigi.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";

export default function MarioLuigi() {
  document.title = "Juani Sabbatini | Projects";
  return (
    <div className="Project">
      <img
        src={marioLuigi}
        alt="Mario vs Luigi Pong"
        className="project-image"
      />
      <div className="project-info">
        <h3 className="project-text">
          Mario vs Luigi Pong es my adaptación del famoso juego. Consiste en un
          juego 1 vs 1 creado con HTML, CSS y JavaScript
          <br />
          <br />
          <span className="languages">HTML</span>
          <span className="languages">CSS</span>
          <span className="languages">JavaScript</span>
        </h3>
        <div className="buttons-project">
          <a
            href="https://github.com/juanisabba/mario-vs-luigi/"
            target="_blank"
            rel="noreferrer"
          >
            Ver en GitHub{" "}
            <FontAwesomeIcon
              icon={faGithub}
              aria-hidden="true"
              className="button-icon"
            />
          </a>
          <a
            href="https://juanisabba.github.io/mario-vs-luigi/"
            target="_blank"
            rel="noreferrer"
          >
            Ver el sitio web{" "}
            <FontAwesomeIcon
              icon={faExternalLinkAlt}
              aria-hidden="true"
              className="button-icon"
            />
          </a>
        </div>
      </div>
    </div>
  );
}
